.footer{
    width: 100%;
    background-color: rgb(44, 44, 44);
    position: absolute;
    bottom: 0;
    opacity: 1;
    transition: all 1s ease-in-out;

    &.open {
        transform: translateY(200px);
        bottom: 199.6px;
        opacity: 0;
        transition: bottom 0.1s; 
        transition: opacity 1s ease-in-out; 
        visibility: hidden;
    }

    &_social{
        margin: 0 auto;
        &-list{
            list-style-type: none;
            display: flex;
            justify-content: center;
            margin: 20px 0 10px 0;
            padding: 0;
            gap: 20px;


            &-item{
                width: 30px;
                img{
                    // color: black;
                    width: 30px;
                }
            }
        }
    }

    &_phone{
        text-align: center;
        font-weight: 300;
        color: rgba(255, 255, 255, 0.5);
        font-size: 16px;
    }

    &_email{
        text-align: center;
        font-weight: 300;
        color: rgba(255, 255, 255, 0.5);
        font-size: 16px;
    }

    &_name{
        margin-top: 10px;
        margin-bottom: 10px;
        text-align: center;
        font-style: italic;
        font-weight: 700;
        color: white;
        font-size: 22px;
    }

    &_copyright{
        margin-top: 20px;
        text-align: center;
        font-weight: 500;
        color: rgba(255, 255, 255, 0.5);
        margin-bottom: 10px;
    }
}

