.social{

    &_title{
        font-size: 60px;
        margin-top: 0;
        margin-bottom: 90px;
        text-align: center;

    }
    &_info{
        align-items: center;
        text-align: center;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        height: auto;
        gap: 50px;
        
    }
    &_github{
        display: flex;
        align-items: center;
        border-radius: 30px;
        width: 300px;
        height: 500px;
        background: linear-gradient(70deg, rgb(40, 40, 40), rgb(50, 50, 50), rgb(60, 60, 60), rgb(70, 70, 70));

        filter: grayscale(100%);
        transition: 0.4s all linear;
        text-decoration: none;
        &:hover{
            background: linear-gradient(70deg, rgb(70, 70, 70), rgb(60, 60, 60), rgb(50, 50, 50), rgb(40, 40, 40));
            filter: grayscale(0%);
            transition: 0.4s all linear;
        }
        &-title{
            margin: 0 auto;
            font-size: 40px;
            font-style: italic;
            font-weight: 400;
            color: #fff;
            
        }
    }
    &_linkedin{
        display: flex;
        border-radius: 30px;
        align-items: center;
        width: 300px;
        height: 500px;
        background: linear-gradient(70deg, rgb(0, 24, 121), rgb(0, 27, 114), rgb(0, 43, 160), #004ae9);

        filter: grayscale(100%);
        transition: 0.4s all linear;
        text-decoration: none;
        &:hover{
            filter: grayscale(0%);
            transition: 0.4s all linear;
        }
        &-title{
            margin: 0 auto;
            font-size: 40px;
            font-style: italic;
            font-weight: 500;
            color: #fff;
            
        }
    }
    &_instagram{
