.biography{
    &_wrapper{
        padding: 0 200px;

    }
    & h2{
        text-align: left;
        color: #fff;
        padding-left: 100px;
    }
    &_info-items{
        list-style-type: none;
        
    }

    &_info-item{
        margin-bottom: 10px;
        & span{
            color: #fff;
            font-weight: 500;
        }
    }

    &_info-main{
        align-items: center;
        display: flex;
        justify-content: center;
        gap: 20px;
        padding: 0 30px;

        &-img{
            img{
                border-radius: 20px;
                width: 550px;
            }
        }
        &-text{
            display: flex;
            flex-direction: column;
            gap: 10px;
            color: #cccccc;
            font-size: 24px;
            font-style: italic;
            font-weight: 300;
        }
    }
    
}