h2{
    color: #fff;
    text-align: center;
    font-size: 70px;
}

h3{
    margin-top: 100px;
    color: #fff;
    text-align: center;
    font-size: 60px;
}

.field{

    &_wrapper{

    }

    &_info{

        &-text{
            padding: 0 200px;
            font-size: 30px;
            color: #fff;
            font-style: italic;
            font-weight: 200;
        }

        &-galery{

            &-main{
                display: flex;
                justify-content: center;
                img{
                    border-radius: 20px;
                    width: 90%;
                    transition: 0.4s all linear;
                    filter: grayscale(100%);

                    &:hover{
                        transition: 0.4s all linear;
                        filter: grayscale(0%);
                    }
                }
            }
            &-other{
                padding-bottom: 100px;
                display: flex;
                justify-content: left;
                flex-direction: column;
                flex-wrap: wrap;
                font-weight: 200;
                gap: 40px;

                span{
                    margin-left: 50px;
                    color: #fff;
                } 

                &-item{
                    &-title{
                        font-style: italic;
                        
                        color: #fff9;
                        font-size: 34px;
                    }
                }
            }

            &-devices{
                padding-bottom: 100px;
                display: flex;
                justify-content: left;
                flex-direction: column;
                flex-wrap: wrap;
                font-weight: 200;
                gap: 40px;

                span{
                    margin-left: 50px;
                    color: #fff;
                }

                &-item{
                    &-title{
                        font-style: italic;
                        
                        color: #fff9;
                        font-size: 34px;
                    }
                }
            }
        }
    }
}

.devices_wrapper{
    margin-top: 50px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}