*{
    font-family: 'Roboto';
}

body{ 
    // overflow: hidden;
    background: linear-gradient(-45deg, #333333, #3d3d3d, #1a1a1a);
    animation: gradient 15s ease infinite;
    background-size: 700% 700%;


    @keyframes gradient {
        0% {
            background-position: 0% 50%;
        }
        50% {
            background-position: 100% 50%;
        }
        100% {
            background-position: 0% 50%;
        }
    }
}
