.navbar {
    border-radius: 15px;
    padding: 0px 100px 0 60px;
    display: flex;
    justify-content: space-between;
    width: 80%;
    height: 80px;
    align-items: center;
    margin: 40px auto;
    transition: all 1s ease-in-out;

    & .open{
        transform: translateY(-120px);
        transition: all 1s ease;
    }
    
}

.navbar-toggle-icon {
    cursor: pointer;

}
.navbar-menu{
    list-style-type: none;
    display: flex;
    gap: 30px;   
    transition: all 1s ease-in-out;
}
  
.navbar-menu-item a{
    font-family: "Roboto";
    border-radius: 5px;
    padding: 10px 30px;
    font-weight: 300;
    width: 40px;
    background-color: rgba(58, 58, 58, 0);
    height: 30px;
    font-size: 18px;
    color: #fff;
}

.navbar-menu-item a:link{
    text-decoration: none;
    color: #fff;

}

.navbar-menu-item a:hover{
    background-color: #fff;
    color: black;
}

